* {
  margin: 0;
  padding: 0;
}

.HomePage {
  display: flex;
}

body {
  background-color: rgb(62, 150, 120);
}

.login {
  display: flex;
  width: 30%;
  height: 40%;
  top: 50%;
  left: 50%;
  position: absolute;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  border-radius: 50px;
  background: #e0e0e0;
  box-shadow:  26px 26px 23px #6e6e6e, -26px -26px 23px #ffffff;
}

.login input {
  display: flex;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%)
}

.login p {
  display: flex;
  color: black;
  margin: 0;
  position: absolute;
  top: 30%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%)
}

.login button {
  width: 100px;
  height: 30px;
  margin: 0;
  position: absolute;
  top: 75%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%)
}

.login input {
  border-color: #cccccc;
  padding: 10px;
  font-size: 18px;
  text-align: center;
  border-width: 3px;
  border-radius: 12px;
  border-style: solid;
  text-shadow: 0px 0px 4px rgb(42 42 42 / 75%);
}
