body {
  background-color: rgb(37, 36, 36);
}

h2 {
  color: white;
}

h4 {
  color: white;
}

p {
  color: white;
  margin-bottom: 10px;
}

h3 {
  color: white;
  font-size: 16px;
  margin-bottom: 10px;
}

a {
  text-decoration: none;
  color:rgb(192, 149, 149);
}

.title {
  margin-top: 30px;
  text-align: center;
}
  
.perfil {
  width: 10%;
  border-radius: 160px;
  position: absolute;
  top: 0;
  right: 0;
}

article {
  text-align: center;
  top: 50%;
  left: 50%;
  position: absolute;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

.icons {
  display: flex;
  flex-direction: column;
  column-gap: 25px;
}

.icon {
  width: 60px;
  height: 58px;
  margin-bottom: 20px;
  margin-left: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: 26px;
  color: #fff;
  border-radius: 50%;
  outline: 2px solid #fff;
  transition-property: outline-offset, outline-color, background-color;
  transition-duration: .25s;
}

.icon:hover {
    outline-offset: 4px;
}

.icon:hover i {
    animation: shake .25s;
}

.icon--instagram:hover {
    background-image: radial-gradient(circle at 30% 107%,
    #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%,
    #285AEB 90%);
    outline-color: #a02d76;
}

.icon--linkedin:hover  {
    background-color: #0077b5;
    outline-color: #0077b5;
}

.icon--github:hover  {
    background-color: #2ea44f;
    outline-color: #2ea44f;
}

@keyframes shake {
    10%{transform: rotate(15deg)}
    20%{transform: rotate(-15deg)}
    30%{transform: rotate(15deg)}
    40%{transform: rotate(-15deg)}
}

.footer {
  text-align: center;
  top: 90%;
  left: 50%;
  position: absolute;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

.link {
  text-align: center;
  top: 85%;
  left: 50%;
  position: absolute;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

.projects {
  display : flex;
}

.project {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  margin-left: auto;
  text-align: center;
  align-items: center;
}

.imgProject {
  width: 70%;
}
